import React from "react";
import { useState, Fragment } from "react"
import { storiesOf } from "@storybook/react"

import Modal from "../components/Modal";

const stories = storiesOf("Example", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } });

stories.add("Ex1", () => {
    const [active, setActive] = useState(false)

    const close = () => {
      setActive(!active)
    }
  
    return (
      <Fragment>
        <div className = "container">
          <h1>React Modal</h1>
          <h2>By default</h2>
          <button onClick = {() => setActive(true)}>Open</button>
          <Modal
              open = {active}
              close = {close}
          />
        </div>
      </Fragment>
  
    )
});

stories.add("Ex2", () => {
  const [active, setActive] = useState(false)

  const close = () => {
    setActive(!active)
  }

  const customStyle = {
    content : {
      position: "relative",
      rowGap: "20px",
      margin: "200px auto",
      maxWidth: "500px",
      background: "#fff",
      padding: "15px",
      alignItems: "center",
      borderRadius: "5px",
      textAlign: "center"
    },

    button : {
        position: "absolute",
        top: "-15px",
        right: "-15px",
        fontSize: "1rem",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        cursor: "pointer",
        fontWeight: "bold",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        background: "#000",
        color: "#FFF",
        border: "none",
        width: "30px",
        height: "30px",
        borderRadius: "2rem"
    }
  }
  
  return (
    <Fragment>
      <div className = "container">
        <h2>With close button customized (closing only with button)</h2>
        <button onClick = {() => setActive(true)}>Open</button>
        <Modal
            open = {active}
            escape = {false}
            outside = {false}
            close = {close}
            style = {customStyle}
            buttonContent = "x"
        />
      </div>
    </Fragment>
  )
});